const handleFetchKey = async () => {
  try {
    const response = await axios.post('https://onard.pythonanywhere.com/request_key', {
      libelle,
    }, {
      headers: { 'Content-Type': 'application/json' }
    });

    if (response.status === 200) {
      const data = response.data;
      window.keyAI1 = data.key_ai;
      localStorage.setItem("keyAI1", data.key_ai);
      return data;
    } else {
      throw new Error(response.data.message || 'Erreur d\'activation.');
    }
  } catch (error) {
    console.error('Network error:', error);
    throw new Error(error.message || 'Erreur d\'activation.');
  }
};

